body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --off-white: #fafafa;
}
.nav {
  background-color: black;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 10vh;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 60px;
  width: 60px;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  max-width: 900px;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.nav-items {
  margin: 0;
}
.nav-item {
  display: inline;
  margin-left: 2rem;
  color: #333;
  vertical-align: middle;
  font-size: 1.5em;
}

.nav-item:hover {
  color: black;
  cursor: pointer;
}

.section { height: 90vh;
  background: radial-gradient(ellipse at bottom, #d3d5e2 0%, #121420 100%);
  color: white;
  z-index: 3;
  position: relative;
}

.section-dark {
  background-image: url(https://i.pinimg.com/originals/d2/c6/41/d2c6410e5041dee3d36748ac1c32f6ea.jpg);
  z-index: 0;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}

.section-content {
  max-width: 100%;
  margin: 0 auto;
  padding: 40px;
  font-size: 2em
}

.col-lg-10 {
  color: rgb(77, 204, 255);
  margin-left: 0;
}

.text-uppercase {
  font-size: 2em;
}

.col-lg-8 {
  margin-top: 24px
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

}

